import React, { Component } from 'react';

import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';

import FormLabel from '@material-ui/core/FormLabel';
import Button from '@material-ui/core/Button';


const electron = window.require('electron');
const ipcRenderer  = electron.ipcRenderer;


const styles = {
  root: {
    width: '100%',
    padding: 24,
    display: 'flex',
    flexDirection: 'column',
  },
  button: {
    position: 'absolute',
    top: 6,
    right: 12,
    zIndex: 10000,
  }
}


class Settings extends Component {
  state = {  data: { display: true, sleep: true }  }

  componentDidMount() {
    this.sub = ipcRenderer.on('set_settings', this.handleData)
    ipcRenderer.send('get_settings');
  }

  componentWillUnmount() {
    this.sub.removeListener('set_settings', this.handleData)
  }

  handleData = (e, data) => {
    this.setState({ enabled: true, data })
  }

  onChangeValue = (key, value) => {
    this.setState({ 
      data: { ...this.state.data, [key]: value }
    });
  }

  handleSave = () => {
    ipcRenderer.send('save_settings', this.state.data);
  }

  render() {
    return (
      <div style={styles.root}>
       <Button color="primary" variant="contained" style={styles.button} onClick={this.handleSave} >Сохранить</Button>
       <FormLabel style={styles.flabel} component="legend">Энергосбережение</FormLabel>
        <FormControlLabel control={<Checkbox color="primary" onChange={(e) => this.onChangeValue('display', e.target.checked)} checked={this.state.data.display} />} label="Всегда активный экран​"/>
        <FormControlLabel control={<Checkbox color="primary" onChange={(e) => this.onChangeValue('sleep', e.target.checked)} checked={this.state.data.sleep} />} label="Работать в фоновом режиме"/>
      </div>
    );
  }
}


export default Settings;